.Splash {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.Splash h1 {
  margin: 20px;
}

.Splash > div {
  margin: auto;
  border: 3px solid black;
}

.Splash form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Splash form div {
  margin: 10px;
}

input {
  margin: 0 10px;
}



